@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.dropdown {
    font-family:'Montserrat';
    border-radius: 5px;
    margin-bottom: 35px;
    text-align: left;
}
.content-dropdown {
    align-items: center;
    background-color: #ff6060;
    border-radius: 5px;
    display: inline-flex;
    height: 25%;
    min-height: 50px;
    width: 100%;
}
  
.titre-dropdown {
    color: #ffffff;
    height: 50px;
    line-height: 50px;
    margin: 5px 10px;
}
  
.iconFleche-dropdown {
    display: flex;
    justify-content: flex-end;
    margin-right: 10px;
    width: 100%;
}

.iconFleche-dropdown.true {
    justify-content: start;
    transform: rotate(-180deg);
}
  
.description-dropdown {
    background-color: #f6f6f6;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    color: #ff6060;
    height: 75%;
    margin-top: -10px;
    min-height: 100px;
    padding: 25px 10px 10px 10px;
}
  
  