@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.banner {
    border-radius: 25px;
    height: 223px;
    margin-bottom: 35px;
    margin-left:  5%;
    margin-right: 5%;
    position: relative;
    top : 171px;
    margin-bottom: 200px;
}
  
.banner-image {
    border-radius: 25px;
    height: 100%;
    object-fit: cover;
    width: 100%;
}
  
.banner-ombre {
    background-color: #000000;
    border-radius: 25px;
    height: 100%;
    left: 0;
    opacity: 0.3;
    position: absolute;
    top: 0;
    width: 100%;
}
  
.banner-text {
    font-family:'Montserrat';
    color: #ffffff;
    font-size: 48px;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
}

@media screen and (max-width: 500px) {
    .banner-text {
      font-size: 30px;
      left: 9%;
      transform: translateY(-50%);
      width: 217px;
    }
  }